<template>
  <v-container fill-height>
    <v-row justify="center" align="center" v-if="formEnable">
      <v-col cols="12" xs="4">
        <v-text-field
          label="Long URL"
          placeholder="Paste Long URL to here!"
          outlined
          append-outer-icon="fa-search-minus"
          v-model="longURL"
          v-on:keyup.enter="shortenURL"
          v-on:click:append-outer="shortenURL"
          autofocus
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row justify="center" align="center" v-else>
      <v-col cols="12" xs="4">
        <v-text-field
          v-model="shortURL"
          append-icon="fa-clipboard"
          append-outer-icon="fa-file"
          filled
          readonly
          label="Min URL"
          type="text"
          @focus="$event.target.select()"
          @click="$event.target.select()"
          @click:append="copyToClickboard"
          @click:append-outer="newURL"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-snackbar v-model="snackbar" centered>
      <h3>{{ errorText }}</h3>

      <template v-slot:action="{ attrs }">
        <v-btn
          v-bind:color="btnColor"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
function validURL(str) {
  let regexp = new RegExp(/^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/);
  if (regexp.test(str)) {
    return true;
  } else {
    return false;
  }
}

export default {
  name: "HelloWorld",

  data: () => ({
    formEnable: true,
    longURL: "",
    shortURL: "",
    errorText: "",
    snackbar: false,
    btnColor: "red",
  }),

  methods: {
    async shortenURL() {
      if (!validURL(this.longURL)) {
        this.btnColor = "red";
        this.errorText =
          "Input is not a correct URL. Please check your input URL!";
        this.snackbar = true;
        return;
      }

      this.axios
        .post("/management/create", {
          url: this.longURL,
        })
        .then((response) => {
          if (!response.data.result) {
            this.btnColor = "red";
            this.errorText = "Not Validated!";
            this.snackbar = true;
          } else {
            this.shortURL = response.data.short_url;
            this.formEnable = false;
          }
        })
        .catch((error) => {
          this.btnColor = "red";
          this.errorText = "Error Happens! Error: " + error;
          this.snackbar = true;
        });
    },
    copyToClickboard() {
      this.$copyText(this.shortURL).then(
        () => {
          this.btnColor = "green";
          this.errorText = "Copied to your clipboard! Enjoy!";
          this.snackbar = true;
        },
        () => {
          this.btnColor = "red";
          this.errorText =
            "Sorry cannot copied to your clipboard! Please copy manually!";
          this.snackbar = true;
        }
      );
    },
    newURL() {
      this.longURL = "";
      this.formEnable = true;
    },
  },
};
</script>
